export function filterVideoResolution(width, obj) {
    if(typeof obj === 'string') {
        try {
            obj = JSON.parse(obj)
        } catch (e) {}
    }

    if(typeof obj === 'object') {
        const resolution = Object.keys(obj || [])
            .map(Number)
            .filter(Boolean)
            .sort((a, b) => {
                a = Math.abs(width * 2 - a);
                b = Math.abs(width * 2 - b);

                return a > b ? 1 : a < b ? -1 : 0;
            })
            .map(String)[0];

        if(resolution) {
            return obj[resolution]
        }
    }

    return obj
}

export function st() {
    return (window.pageYOffset !== undefined) ?
        window.pageYOffset :
        (
            document.documentElement ||
            document.body.parentNode ||
            document.body
        ).scrollTop;
}

export function selectAll(selector, container) {
    return container ?
        Array.prototype.slice.call(Array.isArray(selector) ?
            selector
                .map(function (s) {
                    return container.querySelectorAll(s)
                })
                .map(function (nl) {
                    return Array.prototype.slice.call(nl) || []
                })
                .reduce(function (acc, val) {
                    return acc.concat(val)
                }, []) :
            container.querySelectorAll(selector)) :
        []
}

