import {st, isFunction} from './_helpers'

const Page = {
    prev: null,
    current: null,
    loading: false,

    firstPath: location.pathname,
    hasHistory:
        window.History &&
        (window.history instanceof History) &&
        isFunction(history.pushState),

    scripts: [],
    cache: {},
    handlers: [
        function(page) {
            let parser = new DOMParser();
            let doc = parser.parseFromString(page.body, "text/html");

            if(doc) {
                [...doc.querySelectorAll('script[src]')]
                    .filter(Page.filterScript)
                    .forEach(s => s.remove());
            }
        },
        function(page) {
            if(page.isRoot) {
                return;
            }

            let parser = new DOMParser();
            let doc = parser.parseFromString(page.body, "text/html");

            document.title = page.title;

            function updateMeta(name, content) {
                const t = document.querySelector(name);
                if(t) t.setAttribute('content', content || '')
            }

            updateMeta('meta[name=description]', page.description);
            updateMeta('link[rel=canonical]', page.canonical);
            updateMeta('meta[property="og:title"]', page.ogTitle);
            updateMeta('meta[property="og:url"]', page.ogUrl);

            let children = doc.body.cloneNode(true).children;

            let pageView = Array.prototype
                .slice.call(children)
                .filter(function(item) {
                    return item && item.classList.contains('page-view')
                })[0];

            if(!pageView) return;

            // todo: check next

            if(!pageView.data) {
                pageView.data = {}
            }

            pageView.data.wheelHandler = function() {
                setTimeout(function() {
                    pageView.classList.remove('enter')
                }, 100)
            };

            pageView.data.wheelHandler();

            document.body.append(pageView)
        }
    ],

    linkCache: {},

    filterScript: function(script) {
        const has = ~Page.scripts.indexOf(script.src);

        if(!has) {
            Page.scripts.push(script.src);
        }

        return has;
    },

    toJSON: function(path, t) {
        let text = t.replace(/&amp;/g, '&');

        let title = /<(title)[^>]*>(.*?)<\/\1>/.exec(text);
        let description = /<meta name="description" content="([^"]*)">/.exec(text);
        let canonical = /<link rel="canonical" href="([^"]*)">/.exec(text);
        let ogTitle = /<meta property="og:title" content="([^"]*)"/.exec(text);
        let ogDescription = /<meta property="og:description" content="([^"]*)"/.exec(text);
        let ogUrl = /<meta property="og:url" content="([^"]*)"/.exec(text);
        let body = text.split(/<body[^>]*>/)[1].split('</body>')[0];

        const prop = n => n ? n[1] : '';

        return {
            path,
            title: title ? title[2] : '',
            description: prop(description),
            canonical: prop(canonical),
            ogTitle: prop(ogTitle),
            ogDescription: prop(ogDescription),
            ogUrl: prop(ogUrl),
            body: body
        };
    },
    get: async function(path) {
        let req = this.cache[path];

        if(!req) {
            try {
                req = await fetch(path)
                    .then(text => text.text())
                    .then(body => Page.toJSON(path, body))
                    .then(page => Object.assign({}, page))
            } catch (e) {
                console.error(e);
                return e.responseText
            }

            this.cache[path] = req
            return req;
        }

        if(req && req.path) {
            return new Promise(function(resolve) {
                resolve(req)
            })
        }

        return req
    },
    handle: function(page, view) {
        Page.prev = Page.current;
        this.handlers.forEach(function(handler) {
            handler(page, view);
        });

        Page.current = page;
    },
    go: function(path, handler) {
        let self = this;
        if(self.loading) return;
        self.loading = true;

        const views = document.querySelectorAll('.page-view');
        if(views.length > 1) {
            self.loading = false;
            return;
        }
        Page.firstPath = null;

        if(Page.hasHistory) {
            if('scrollRestoration' in history) {
                history.scrollRestoration = 'manual';
            }

            history.pushState(null, '', path);
        }

        return this.get(path).then(function(page) {
            let view = document.querySelector('.page-view');

            if(!view) {
                self.loading = false;
                return
            }

            if(!view.data) {
                view.data = {}
            }

            view.data['scroll-position'] = st();
            view.classList.add('leave');

            Page.handle(page, view);

            if(isFunction(handler)) {
                handler(page);
            }

            self.loading = false;
        })
            .catch(function(err) {
                self.loading = false;
                console.error(err)
            });
    }
};

export default Page;
