import './_initial';

import pageHandlerContact, {inputsTransliterateHandler} from './_contactPage';
import {lazyLoadNodes, lazyLoadScroll} from './_imgLazy';

import navigation, {headerHandler} from './_navigation.js';
import Page from './_page';
import phVideos, {videoIntersection, visibilityChange} from './_phVideos';
import {workResizeHandler, initWorkVideo} from './_workPage';
import pageLeaveChain from "./_pageLeaveChain";
import TransitionChain from './_TransitionChain';
import {selectAll, getniv, TRANSITION_END_EVENT_TYPE} from './_helpers';
import landingSlider from './_landingSlider';
import sectionCollapse from './_sectionCollapse';

// GTM
window.dataLayer = window.dataLayer || [];
navigation();

document.addEventListener('click', function(event) {
    let t = event.target;
    const link = t.tagName === 'A' && t.getAttribute('href') ? t : t.closest('a[href]');

    if(!link || link.target || event.metaKey || event.ctrlKey) {
        return;
    }

    event.preventDefault();

    if (location.href && (location.origin + location.pathname) === link.href) {
        return;
    }

    if (link.pathname === '/' && window.location.pathname === link.pathname) {
        landingSlider.slide = landingSlider.slides[0];

        return false;
    }

    if (location.origin !== link.origin) {
        if (!link.getAttribute('rel')) {
            link.setAttribute('rel', 'nofollow noopener');
        }
        return open(link, '_blank');
    }

    Page.go(link.pathname + link.search);
}, false);

window.addEventListener('popstate', function() {
    if (location.pathname === Page.firstPath) {
        return;
    }

    location.href = location.href;
});

document.addEventListener(TRANSITION_END_EVENT_TYPE, function(event) {
    const t = event.target;
    const mt = document.querySelector('.page-view.leave .rslider-component .rslide.last-current .rslide-inner');

    if(mt && t && t === mt) {
        event.stopPropagation();
        delete landingSlider.__currentSlide;

        const leave = document.querySelector('.page-view.leave');
        if(leave) leave.remove();
    }
}, false);

Page.handlers.push(headerHandler);

Page.handlers.push(async function(page) {
    if(page?.path) {
        await inputsTransliterateHandler(page.path)
    }
});

Page.handlers.push(function() {
    let notleavepage = document.querySelector('.page-view:not(.leave');

    let slider = landingSlider.slider = notleavepage && notleavepage.querySelector('.rslider-component');

    // off
    landingSlider.offEvents();
    window.removeEventListener('resize', workResizeHandler);

    if (Page.prev && Page.prev.path === '/' && landingSlider.slide) {
        landingSlider.pause(landingSlider.slide);
    }

    let work = notleavepage && notleavepage.querySelector('.work-block');

    if(work) {
        window.addEventListener('resize', workResizeHandler);
    }

    initWorkVideo();

    if(slider) {
        landingSlider.addEvents(slider, notleavepage);
    }
});

lazyLoadScroll();

Page.handlers.push(function(page, leaveView) {
    if(page.path !== '/') {
        let niv = getniv(20)
        if(!niv || !niv.page) return;
        niv.page.classList.add('transition-chain')
        let pageView = niv.page;

        let pageEnterChain = new TransitionChain('page-enter', function () {
            pageView.classList.remove('transition-chain');
            niv.nodes.forEach(n => {
                n.classList.remove('page-enter-complete');
                n.classList.remove('viewport-process');
            })
        });

        lazyLoadNodes(niv.nodes);

        pageEnterChain.chain = niv.nodes.map(function (node, index, list) {
            return {
                node: node,
                handler: function (event) {
                    let state = event.propertyName === (index === list.length - 1 ? 'opacity' : 'visibility');

                    if (state) {
                        this.classList.add('page-enter-complete');
                        this.classList.add('is-in-viewport');
                    }

                    return state;
                }
            };
        });

        setTimeout(() => {
            requestAnimationFrame(() => {
                pageEnterChain.run()
            })
        }, 10);
    }

    if (!Page.current) {
        return;
    }

    if (Page.current.path === '/') {
        if (leaveView) {
            selectAll('.rslide', leaveView).forEach(function(s) {
                landingSlider.pause(s)
            });
        }
    } else {
        pageLeaveChain.run();
    }
});

Page.handlers.push(videoIntersection);
Page.handlers.push(phVideos);
Page.handlers.push(sectionCollapse);
visibilityChange();
Page.handlers.push(pageHandlerContact);

//	pages
!function (page) {
    Page.cache[page.path] = page;

    (function() {
        Page.handle(Object.assign({
            isRoot: true
        }, page));
    })();
}(Page.toJSON(location.pathname, document.documentElement.outerHTML));
