import {getniv, getRoot, st} from './_helpers';
import TransitionChain from './_TransitionChain.js'
import {TRANSITION_END_EVENT_TYPE} from "./_helpers";

const root = getRoot();

function toggleBurgerIcon(base, isBurger) {
    let icon = {
        remove: isBurger ? 'icon-burger' : 'icon-close',
        add: !isBurger ? 'icon-burger' : 'icon-close'
    };
    let target = document.querySelector(base + ' .' + icon.remove);

    if (target !== null) {
        target.classList.remove(icon.remove);
        target.classList.add(icon.add)
    }
}

/**
 * mobile menu: open, close, transition chain
 */

function getHeader() {
    return document.querySelector('.page-view:not(.leave) header');
}

function initMobileNav() {
    let nav = document.querySelector('.navigation');

    let openNavigation = {};
    let closeNavigation = {};
    let navigationNodes = [];

    if(nav) {
        nav.classList.add('is-visible');

        navigationNodes = getniv(0, nav, ['ul li', 'p']).nodes;

        openNavigation = new TransitionChain('open-navigation', function () {
            navigationNodes.forEach(n => {
                n.classList.remove('is-hidden');
                n.classList.remove('is-complete')
            });
        });

        openNavigation.chain = [{
            node: function () {
                let wnd = window;
                let page = document.querySelector('.page-view');
                let scrollTop = wnd.scrollY;

                if (!page.data) page.data = {};
                page.data['scroll-top'] = scrollTop;

                //page.find('header').css('transform', 'translateY(' + wnd.scrollTop() + 'px)');

                let inner = page.querySelector('.page-view-inner');
                if (inner) {
                    inner.style.transform = 'translateY(-' + scrollTop + 'px)'
                }

                wnd.scrollTo(0, 0);

                if (root) {
                    root.classList.add('views-hidden');
                    root.classList.add('navigation-opened');
                }

                nav.classList.add('is-visible');
                setTimeout(() => {
                    nav.classList.add('is-opened');
                }, 100)

                return nav
            },
            handler: function (event) {
                return event.propertyName === 'opacity';
            }
        }].concat(
            navigationNodes
                .map(function (node, index, list) {
                    return {
                        node: node,
                        handler: function (event) {
                            let propName = index === list.length - 1 ? 'opacity' : 'visibility';
                            let state = event.propertyName === propName;

                            if (state) {
                                this.classList.add('is-complete');
                            }

                            return state;
                        }
                    };
                })
        );

        nav.classList.remove('is-visible');

        closeNavigation = new TransitionChain('close-navigation', function () {
            let wnd = window;
            let page = document.querySelector('.page-view');

            if(root !== null) root.classList.remove('views-hidden');
            const inner = page.querySelector('.page-view-inner');
            if(inner) inner.removeAttribute('style');
            wnd.scrollTo(0, page.data['scroll-top']);

            this.classList.remove('is-visible');
        });

        closeNavigation.chain = [function () {
            openNavigation.stop();

            if (root) root.classList.remove('navigation-opened');

            nav.classList.remove('is-opened');
            return nav;
        }];
    }

    return {
        navigationNodes,
        openNavigation,
        closeNavigation
    }
}


/**
 * header scroll: hide, show on scroll
 */

function headerScrollListener() {
    const header = getHeader()
    if(!header) return;

    if(header.data === undefined) {
        header.data = {};
    }

    let height = header.offsetHeight;
    let scrollTop = Math.max(0, st());

    let wndHeight = window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;

    if(header.data.lastScrollTop === undefined) {
        header.data.lastScrollTop = scrollTop
    }

    if(header.data.position === undefined) {
        header.data.position = 0;

        if(scrollTop) {
            header.classList.add('bg-showed')
        } else {
            return
        }
    }

    header.classList[scrollTop ? 'remove' : 'add']('top-of-page');

    if(!scrollTop) {
        header.classList.remove('bg-showed');
        header.removeAttribute('style');
    }

    if(header.classList.contains('animated')) return;

    let deltaY = scrollTop - header.data.lastScrollTop;

    header.data.position = Math.min(
        Math.max(
            -height,
            header.data.position - deltaY + (wndHeight - (header.data.lastWndHeight || wndHeight))
        ),
        0
    );

    if(deltaY < 0 && header.data.position + deltaY === -height) {
        header.classList.add('animated');
        header.classList.add('bg-showed');
        header.data.position = 0;
    }

    header.data.lastScrollTop = scrollTop;
    header.data.lastWndHeight = wndHeight;

    if(scrollTop) {
        header.style.transform = 'translateY(' + header.data.position + 'px)';
    }
}

export default function navigation() {
    let header = document.querySelector('header');

    if(!header) return;

    header.data = {};
    header.data.position = 0;

    const mobileNav = initMobileNav();

    window.addEventListener('scroll', headerScrollListener, false);

    document.addEventListener('click', event => {
        const openbtn = '.open-navigation';
        const t = event.target;

        if (t.closest(openbtn) || t.classList.contains(openbtn)) {
            event.preventDefault();

            toggleBurgerIcon('header', true);
            toggleBurgerIcon('.navigation', true);

            mobileNav.navigationNodes.forEach(n => {
                n.removeAttribute('data-transition-chain');
                n.classList.remove('is-complete');
                n.classList.remove('transition-chain-state');
                n.classList.add('is-hidden')
            });

            mobileNav.openNavigation.run();
        }

        if (
            t.tagName === 'A' && t.closest('.navigation') ||
            t.closest('.navigation button')
        ) {
            let href = event && event.target && event.target.href;

            if (!href || t.origin === location.origin) {
                toggleBurgerIcon('header', false);
                toggleBurgerIcon('.navigation', false);
            }

            mobileNav.closeNavigation.run();
        }
    }, false);
}

export function headerHandler() {
    const header = getHeader();
    if(header) {
        if(!header.data) {
            header.data = {};
            header.data.position = 0;
        }

        header.addEventListener(TRANSITION_END_EVENT_TYPE, event => {
            event.stopPropagation();
            if(header.classList.contains('animated')) {
                header.classList.remove('animated')
            }
        });
    }
}
