import {getniv} from './_helpers'

/**
 * img lazy loading on scroll
 */
function loaded() {
    this.classList.add('is-loaded')
}

export const lazyLoadNodes = function(nodes = []) {
    const imgs = nodes.map(i => {
        return i.querySelector('img:not(.is-loaded)')
    }).filter(Boolean);

    imgs.forEach(img => {
        if(img && img.hasAttribute('data-src')) {
            img.setAttribute('src', img.getAttribute('data-src'));
            img.removeAttribute('data-src');

            if(img.complete) {
                loaded.call(img);
                loaded.call(img.parentNode);
            } else {
                img.addEventListener('load', () => {
                    loaded.call(img);
                    loaded.call(img.parentNode);
                }, false);
            }
        }
    });
};

export const lazyLoadScroll = function() {
    window.addEventListener('scroll', function () {
        if(document.querySelector('.page-view.leave') !== null) {
            return;
        }

        const nodes = getniv(-150).nodes;

        nodes.forEach(n => {
            n.classList.add('is-in-viewport');

            setTimeout(() => {
                requestAnimationFrame(() => {
                    n.classList.add('page-enter-complete')
                })
            }, 100)
        });

        lazyLoadNodes(nodes);
    }, false);
};
