import {filterVideoResolution} from "./_helpers";

export default function () {
    const touchDevice = 'ontouchstart' in window || navigator.maxTouchPoints;
    const vids = document.querySelectorAll('.page-view:not(.leave) article video');

    function playPauseListener(e) {
        const t = e.target;
        const parent = t && t.closest('.video');

        if (!t.played.length /*|| !t.__in_viewport*/ || document.hidden) return;

        if(parent) {
            parent.classList.remove('played');
            parent.classList.remove('paused');
            setTimeout(() => {
                requestAnimationFrame(() => {
                    parent.classList.add(t.paused ? 'paused' : 'played');
                })
            }, 10)
        }
    }

    function loadedDataListener(e) {
        const t = e.target;
        const parent = t && t.closest('.video');
        if(touchDevice) t.setAttribute('controls', 'true');
        if(parent) parent.classList.add('loaded');
    }

    vids.forEach(i => {
        i.addEventListener('loadeddata', loadedDataListener, false);
        // i.addEventListener('play', playPauseListener, false);
        // i.addEventListener('pause', playPauseListener, false);
        i.addEventListener('click', function(event) {
            i[i.paused ? 'play' : 'pause']();
            playPauseListener(event);
        }, false);
    });
}

function io(target, fn) {
    // const threshold = [];
    //
    // for(let i=0; i <= 1.0; i += 0.01) {
    //     threshold.push(i);
    // }

    let observerOptions = {
        root: null,
        rootMargin: "0px",
        threshold: [0, 1]
    };

    function intersectionCallback(entries) {
        entries.forEach((entry) => {
            fn(entry.target)[entry.isIntersecting]();
        });
    }

    const observer = new IntersectionObserver(intersectionCallback, observerOptions);
    observer.observe(target);
}

export function videoIntersection(/*page, leaveView*/) {
    const nodes = document.querySelectorAll('.page-view:not(.leave) article .video:not(.paused) video');

    function intersecting(video) {
        return {
            true: function() {
                const dataSrc = video.getAttribute('data-src');

                if(dataSrc) {
                    const width = window.innerWidth || 1440;
                    const obj = JSON.parse(dataSrc);
                    const src = filterVideoResolution(width, obj);

                    video.setAttribute('src', src);
                    video.removeAttribute('data-src');
                }

                if(video.paused) {
                    requestAnimationFrame(() => {
                        video.play()
                    })
                }
            },

            false: function() {
                if(video.played) {
                    requestAnimationFrame(() => {
                        video.pause()
                    })
                }
            }
        }
    }

    nodes.forEach(node => {
        io(node, intersecting)
    })
}

// article video play/pause on visibility change
export function visibilityChange() {
    document.addEventListener('visibilitychange', function() {
        const vids = document.querySelectorAll('.page-view:not(.leave) article .video:not(.paused) video');

        vids.forEach(node => {
            node[document.hidden ? 'pause' : 'play']()
        })
    }, false);
}
