function interval(cb, request, cancel, requestParameter) {
    let requestId;
    let previousLoopTime;

    function loop(now) {
        requestId = request(loop, requestParameter);
        cb(now - (previousLoopTime || now));
        previousLoopTime = now;
    }

    return {
        start() {
            if (!requestId) {
                loop(0);
            }
        },
        stop() {
            cancel(requestId);
            requestId = null;
            previousLoopTime = 0;
        }
    };
}

function frameInterval(cb) {
    return interval(cb, requestAnimationFrame, cancelAnimationFrame);
}

export default (video, opts = {}) => {
    const canvas = opts.canvas || document.createElement('canvas');
    const ctx = canvas.getContext('2d');

    const drawCall = opts.drawCall || function () {
        ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
    };

    if (opts.updateSize !== false) {
        const updateSize = () => {
            canvas.width = video.videoWidth;
            canvas.height = video.videoHeight;
        };

        video.addEventListener('loadedmetadata', updateSize);
        updateSize();
    }

    const updater = frameInterval(() => drawCall(ctx, video));

    video.addEventListener('playing', updater.start);
    video.addEventListener('pause', updater.stop);

    video.addEventListener('abort', updater.stop);
    video.addEventListener('error', updater.stop);
    video.addEventListener('waiting', updater.stop);

    if (!video.paused) {
        updater.start();
    }

    return canvas;
};
