import {getParams, getRoot, uuid} from './_helpers';
import cookie from 'cookie_js';

/**
 * add class is-touch/no-touch to root
 */
(function() {
    let root = getRoot();
    let cls = 'ontouchstart' in window || navigator.maxTouchPoints ? 'is-touch' : 'no-touch';

    function fontsLoadEvent() {
        root.classList.add(cls);
        root.classList.add('fonts-loaded');
        root.classList.remove('fonts-loading');
    }

    if(document.fonts && 'ready' in document.fonts) {
        document.fonts.ready.then(({status}) => {
            if(status === 'loaded') {
                fontsLoadEvent()
            }
        })
    } else {
        requestAnimationFrame(fontsLoadEvent)
    }
})();

(function () {
    let referrer;
    if (document.referrer) {
        referrer = new URL(document.referrer);
    }

    let query = getParams(location.search);

    /**
     * @property {string} utm_source
     * @property {string} utm_medium
     * @property {string} utm_campaign
     * @property {string} utm_internal_source
     */

    if (query.utm_source) {
        cookie.set('source', query.utm_source);

        if (query.utm_medium) {
            cookie.set('medium', query.utm_medium);
        }
        if (query.utm_campaign) {
            cookie.set('campaign', query.utm_campaign);
        }
    } else if(query.utm_internal_source) {
        cookie.set('internal_source', query.utm_internal_source);
    } else if (referrer && referrer.host !== location.host) {
        cookie.set('source', referrer.hostname.replace(/^www\./, ''));
        cookie.set('medium', 'referral');
        cookie.set('campaign', referrer.pathname.split('/').filter(function (value) {
            return value;
        }).pop());
    }

    let fuid = cookie.get('fuid');

    if(!fuid) {
        const generatedFuid = uuid();
        cookie.set('fuid', generatedFuid);
    }

    /**
     *  crazy egg custom variables
     * @property CE2 crazy egg
     */
    window.CE_READY = function CE_READY() {
        window.CE2.set(1, fuid)
    };
})();
