import TransitionChain from "./_TransitionChain";

let pageLeaveChain = new TransitionChain('page-leave', function () {
    this.remove();
});
pageLeaveChain.chain = [{
    node: function () {
        const page = document.querySelector('.page-view.leave');

        if(page) {
            const pageViewInner = page.querySelector('.page-view-inner');

            if(pageViewInner) {
                pageViewInner.style.transform = 'translateY(-' + page.data['scroll-position'] + 'px)'
            }

            window.scrollTo(0, 0);
        }

        return page
    },
    handler: function (event) {
        return event.propertyName === 'opacity';
    }
}];

export default pageLeaveChain;
