import Page from './_page';
import {TRANSITION_END_EVENT_TYPE} from './_helpers';
import videoCanvas from './_frameInterval'

function oneOfTime(handler, duration) {
    let state;

    return function() {
        if(state) {
            return;
        }

        state = true;

        handler.apply(this, arguments);

        setTimeout(function() {
            state = null;
        }, duration);
    }
}

let scheduledAnimationFrame = false;

let landingSlider = {
    videoDelay: 2000,

    play: function(slide, isFirst) {
        if(!slide) {
            return;
        }

        slide.classList.add('is-current');

        if(isFirst) {
            setTimeout(() => {
                this.slider && this.slider.classList.add('activated');
            }, 60)
        }

        let component = this.slider;
        const theme = slide.getAttribute('data-theme');
        if(theme) {
            component.setAttribute('data-theme', theme);
        }

        requestAnimationFrame(() => {
            component.classList.add('animated');

            const current = this.slides.indexOf(slide);
            if(current > -1) {
                this.pagination[current].classList.add('is-current');
            }

            const header = document.querySelector('.page-view:not(.leave) header');

            if(header) {
                if(theme) {
                    header.setAttribute('data-theme', theme);
                }
                header.classList.remove('bg-showed');
                header.classList.remove('top-of-page');
            }

            // clearTimeout(landingSlider.videoTimer);

            if(!slide.classList.contains('is-loaded')) {
                return landingSlider.loadMedia(slide, isFirst);
            }

            //landingSlider.videoTimer = setTimeout(function() {
            landingSlider.playMedia(slide);
            //}, landingSlider.videoDelay);
        });
    },
    pause: function(slide) {
        if(!slide) {
            return;
        }

        if(this.__lastCurrentSlide) {
            this.__lastCurrentSlide.classList.remove('last-current');
        }
        this.__lastCurrentSlide = slide;

        if(slide) {
            slide.classList.add('last-current');
            setTimeout(() => {
                slide.classList.remove('is-current');
            }, 60)

            const c = slide.querySelector('canvas');
            if(c) {
                if(!c.data) c.data = {}
                let video = c.data.video;

                if(video) {
                    video.pause();
                }
            }
        }

        const i = this.slides.indexOf(slide);
        if(i > -1) this.pagination[i].classList.remove('is-current');
    },

    finally: function() {
        if(this.slider) this.slider.classList.remove('animated');
    },

    loadMedia: function(slide, isFirst) {
        //	image
        [...slide.querySelectorAll('img[data-src]')]
            .forEach(function(img, i) {
                img.addEventListener('load', function(e) {
                    e.target.classList.add('is-loaded');
                    landingSlider.mediaLoaded(slide, isFirst);
                    landingSlider.loadNext(slide);
                });

                // img.setAttribute('loading', 'lazy');

                // if(img.parentNode.classList.contains('rslide-background')) {
                //     img.setAttribute('width', '2880');
                //     img.setAttribute('height', '2160');
                // }

                img.setAttribute('src', img.getAttribute('data-src'));
                img.removeAttribute('data-src');
            });

        //	video
        const canvas = slide.querySelector('canvas[data-url]');

        if(canvas) {
            let link = canvas.getAttribute('data-url');

            if(!slide.data) slide.data = {};
            slide.data.canvas = canvas;

            if(!canvas.data) canvas.data = {}

            canvas.data.context = canvas.getContext('2d');
            canvas.removeAttribute('data-url');

            if(!(link in Page.linkCache)) {
                // Page.linkCache[link] = $.get(link);
                Page.linkCache[link] = link;
            }

            if(!('ontouchstart' in window || navigator.maxTouchPoints)) {
                setTimeout(function() {
                    let video = document.createElement('video');

                    canvas.data.video = video;

                    video.controls = true;
                    video.muted = true;
                    const loop = canvas.getAttribute('loop') !== undefined;
                    if(loop) video.loop = true;

                    video.addEventListener('canplaythrough', function canplayhandler(e) {
                        if(video.readyState === video.HAVE_ENOUGH_DATA) {
                            canvas.setAttribute( 'width', `${video.videoWidth}`)
                            canvas.setAttribute( 'height', `${video.videoHeight}`)

                            landingSlider.mediaLoaded(slide, isFirst);
                            if(isFirst || slide.classList.contains('is-current')) {
                                landingSlider.playMedia(slide);
                            }

                            canvas.classList.add('is-loaded');

                            e.target.removeEventListener('canplaythrough', canplayhandler)
                        }
                    }, false);

                    video.src = link;

                    if(video.readyState === video.HAVE_ENOUGH_DATA) {
                        if(isFirst) video.oncanplay();
                        landingSlider.loadNext(slide);
                    }
                }, 300);
            } else {
                let image = new Image;

                // image.src = data.thumb; // TODO

                image.onload = function() {
                    this.classList.add('is-loaded');
                    landingSlider.mediaLoaded(slide, isFirst);
                };

                if(image.complete) {
                    image.onload();
                }

                canvas.replaceWith(image);
            }
        }
    },

    loadNext: function(slide) {
        const index = landingSlider.slides.indexOf(slide);

        if(landingSlider.slides[index + 1]) {
            landingSlider.loadMedia(landingSlider.slides[index + 1])
        }
    },

    mediaLoaded: function(slide, isFirst) {
        slide.classList.add('is-loaded');

        if(isFirst) {
            // this.slider && this.slider.classList.add('activated');
            this.finally();
        }
    },
    playMedia: function(slide) {
        let canvas = landingSlider.getCanvas(slide);
        let video = landingSlider.getVideo(slide);

        if(video) {
            videoCanvas(video, {canvas});
            video.currentTime = 0;
            canvas.classList.add('is-played');
            video.play();
        }
    },
    getCanvas: function(slide) {
        return slide.data && slide.data.canvas
    },
    getVideo: function(slide) {
        let canvas = landingSlider.getCanvas(slide);

        if(!canvas) {
            return;
        }

        return canvas.data.video;
    },

    get slide() {
        return this.__currentSlide;
    },

    set slide(newSlide) {
        let slide = this.slide;
        let isDirection = typeof newSlide === 'string' && !!slide;
        let direction = newSlide;

        if(isDirection) {
            const next = newSlide === 'next' ?
                'nextElementSibling' :
                newSlide === 'prev' ?
                    'previousElementSibling' :
                    undefined;
            newSlide = next ? slide[next] : landingSlider.slider.firstChild;
        }
        if(isDirection && direction === 'next' && !newSlide && slide) {
            return
        }

        if(!newSlide || slide && slide === newSlide) {
            return;
        }

        this.pause(slide);
        this.__currentSlide = newSlide;
        this.play(newSlide, typeof slide === 'undefined' || slide.previousSibling !== null);
    },

    transitionHandler: function(event) {
        event.stopPropagation();
        const t = event.target;
        const cl = t && (t.closest('.rslide.last-current') || t.closest('.rslide.is-current'));
        const an = t && t.closest('.rslider-component.animated');

        if(event.target === event.currentTarget && cl && an) {
            landingSlider.finally();
        }
    },

    mouseDownHandler: function(event) {
        if(event.which === 3) {
            return;
        }

        const that = this;

        that.classList.add('dragged');
        if(!that.data) that.data = {};
        that.data.mouseStart = event.screenY;
    },
    mouseMoveHandler: function(event) {
        const that = this;
        if(!that || !that.classList.contains('dragged')) return;
        if(!that.data) that.data = {};
        that.data.mouseEnd = event.screenY;
    },
    mouseUpHandler: function() {
        let that = landingSlider.slider;
        if(!that) return;

        that.classList.remove('dragged');

        if(
            that.data && that.data.mouseEnd && that.data.mouseStart &&
            that.data.mouseEnd - that.data.mouseStart &&
            !that.classList.contains('animated')
        ) {
            landingSlider.slide = (that.data.mouseEnd - that.data.mouseStart) < 0 ? 'next' : 'prev';
        }

        if(!that.data) that.data = {};

        that.data.mouseStart = null;
        that.data.mouseEnd = null;
    },

    touchStartHandler: function(event) {
        const that = this;
        that.classList.add('dragged');
        if(!that.data) that.data = {};
        that.data.touchStart = event.touches[0].pageY;
    },
    touchMoveHandler: function(event) {
        event.preventDefault();
        const that = this;
        if(!that.classList.contains('dragged')) return;
        if(!that.data) that.data = {};
        that.data.touchEnd = event.touches[0].pageY;
    },
    touchEndHandler: function() {
        let that = this;
        that.classList.remove('dragged');

        if(
            that.data.touchEnd &&
            that.data.touchEnd - that.data.touchStart &&
            !that.classList.contains('animated')
        ) {
            landingSlider.slide = (that.data.touchEnd - that.data.touchStart) < 0 ? 'next' : 'prev';
        }

        that.data.touchStart = null;
        that.data.touchEnd = null;
    },

    keyHandler: function(event) {
        if(!landingSlider.slider) {
            return;
        }

        if(!landingSlider.slider.data) {
            landingSlider.slider.data = {}
        }

        if(!landingSlider.slider.data.keyHandler) {
            landingSlider.slider.data.keyHandler = oneOfTime(function(direction) {
                if(!landingSlider.slider.classList.contains('animated')) {
                    landingSlider.slide = direction;
                }
            }, 500);
        }

        switch(event.keyCode) {
            case 38:	//	up
                event.preventDefault();
                landingSlider.slider.data.keyHandler('prev');
                break;
            case 40:	//	down
                event.preventDefault();
                landingSlider.slider.data.keyHandler('next');
                break;
        }
    },

    paginationHandler: function(event) {
        event.preventDefault();
        const li = event.target.closest('li');
        const index = landingSlider.pagination.indexOf(li);
        if(index > -1) landingSlider.slide = landingSlider.slides[index];
    },

    wheelHandler: function(event) {
        event.preventDefault();

        if (scheduledAnimationFrame) {
            return;
        }

        scheduledAnimationFrame = true;

        requestAnimationFrame(() => {
            if(!landingSlider.slider || landingSlider.paused) {
                scheduledAnimationFrame = false;
                return;
            }

            let deltaY = -event.wheelDelta / 3 || event.deltaY || event.detail;
            let delta = Math.abs(deltaY);

            if(!(landingSlider.wheelDeltaList instanceof Array)) {
                landingSlider.wheelDeltaList = [];
            }

            let list = landingSlider.wheelDeltaList;

            if(
                !list.length &&
                !(delta % 10) &&
                (!landingSlider.lastWheelTime || (event.timeStamp - landingSlider.lastWheelTime) > 500)
            ) {
                landingSlider.paused = true;

                setTimeout(function() {
                    landingSlider.paused = false;
                }, 500);
            } else {
                if(!(delta % 10)) {
                    scheduledAnimationFrame = false;
                    return;
                }

                if(list.length && delta < list[list.length - 1]) {
                    list.splice(0, list.length);
                }

                if(delta !== list[list.length - 1]) {
                    list.push(delta);
                }

                if(list.length < 3 || landingSlider.slider.classList.contains('animated')) {
                    scheduledAnimationFrame = false
                    return;
                }

                list.splice(0, list.length);

                landingSlider.paused = true;

                setTimeout(function() {
                    landingSlider.paused = false;
                }, 350);
            }

            landingSlider.lastWheelTime = event.timeStamp;
            landingSlider.slide = deltaY < 0 ? 'prev' : 'next';

            scheduledAnimationFrame = false;
        });
    },

    resizeHandler: function() {
        let that = landingSlider.slider;
        that.style.height = '';

        if(!that) {
            return;
        }

        let height = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);
        if(height !== window.innerHeight) {
            that.style.height = height
        }
    },

    offEvents: function() {
        let rslidesInner = [...document.querySelectorAll('.rslider-component .rslide .rslide-inner')];
        let rslideComponents = document.querySelector('.page-view:not(.leave) .rslider-component');

        // off
        rslidesInner.forEach(rslideInner => {
            rslideInner.removeEventListener(TRANSITION_END_EVENT_TYPE, landingSlider.transitionHandler, false)
        });

        if(rslideComponents) {
            rslideComponents.removeEventListener('mousedown', landingSlider.mouseDownHandler);
            rslideComponents.removeEventListener('mousemove', landingSlider.mouseMoveHandler);
            rslideComponents.removeEventListener('touchstart', landingSlider.touchStartHandler);
            rslideComponents.removeEventListener('touchmove', landingSlider.touchMoveHandler);
            rslideComponents.removeEventListener('touchcancel', landingSlider.touchEndHandler);
            rslideComponents.removeEventListener('touchend', landingSlider.touchEndHandler);
        }

        ['keydown', 'keypress', 'keyup'].forEach(event => {
            document.removeEventListener(event, landingSlider.keyHandler);
        });

        document.removeEventListener('mouseup', landingSlider.mouseUpHandler);
        document.removeEventListener('wheel', landingSlider.wheelHandler, {passive: false});
        window.removeEventListener('resize', landingSlider.resizeHandler);
    },

    addEvents: function(slider, notleavepage) {
        let slides = landingSlider.slides = [...slider.querySelectorAll('.rslide')];

        let header = landingSlider.header = notleavepage && notleavepage.querySelector('header');
        if(header) header.classList.add('floating');

        const rslidesInner = [...document.querySelectorAll('.rslider-component .rslide .rslide-inner')];
        const notLeaveComponent = document.querySelector('.page-view:not(.leave) .rslider-component');
        const pagination = landingSlider.pagination = slider ? [...document.querySelectorAll('.rslider-pagination li')] : [];

        rslidesInner.forEach(rslideInner => {
            rslideInner.addEventListener(TRANSITION_END_EVENT_TYPE, landingSlider.transitionHandler, false)
        });

        if(notLeaveComponent) {
            notLeaveComponent.addEventListener('mousedown', landingSlider.mouseDownHandler);
            notLeaveComponent.addEventListener('mousemove', landingSlider.mouseMoveHandler);
            notLeaveComponent.addEventListener('touchstart', landingSlider.touchStartHandler);
            notLeaveComponent.addEventListener('touchmove', landingSlider.touchMoveHandler);
            notLeaveComponent.addEventListener('touchcancel', landingSlider.touchEndHandler);
            notLeaveComponent.addEventListener('touchend', landingSlider.touchEndHandler);
        }

        ['keydown', 'keypress', 'keyup'].forEach(event => {
            document.addEventListener(event, landingSlider.keyHandler);
        });

        document.addEventListener('mouseup', landingSlider.mouseUpHandler);
        document.addEventListener('wheel', landingSlider.wheelHandler, {passive: false});

        pagination.forEach(dot => {
            dot.addEventListener('click', landingSlider.paginationHandler, false);
        })

        window.addEventListener('resize', landingSlider.resizeHandler);

        requestAnimationFrame(() => {
            if(slides.length) landingSlider.slide = slides[0];
        })
    }
};

export default landingSlider;
