import {filterVideoResolution} from "./_helpers";

// work video
function isDesktop() {
    return window.innerWidth > 1024
}

function workHoverCard(target, pause) {
    if(!target.classList.contains('.card')) {
        target = target.closest('.card');
    }

    const vid = target.querySelector('video');

    function Pause() {
        if(pause) {
            vid.pause();
            vid.currentTime = 0
        }
    }

    if(vid !== null) {
        let playPromise = vid.play();

        if(playPromise !== undefined) {
            playPromise
                .then(function() {
                    Pause()
                })
                .catch(function(err) {console.error(err)});
        } else {
            Pause()
        }
    }
}

function videoAction(card) {
    const video = card.querySelector('video');

    if(video !== null && card.classList.contains('intersected')) {
        const urls = video.getAttribute('data-src');
        const src = filterVideoResolution(video.offsetWidth, JSON.parse(urls));

        video.setAttribute('src', src);
        video.removeAttribute('data-src');

        video.oncanplay = video.oncanplaythrough = function() {
            if(video.readyState === video.HAVE_ENOUGH_DATA) {
                video.classList.add('loaded');
            }
        };

        card.addEventListener('mouseover', () => {
            workHoverCard(video)
        }, false);
        card.addEventListener('mouseout', () => {
            workHoverCard(video, true)
        }, false)
    }
}

function action(card) {
    card.classList.add('intersected');

    if(isDesktop()) {
        videoAction(card)
    }
}

function getCards(target, selector) {
    return Array.prototype.slice.call(document.querySelectorAll(target))
        .map(function(vid) {
            return vid.closest(selector);
        })
        .filter(Boolean);
}

export function initWorkVideo() {
    let cards = getCards('video[data-src]', '.card');

    if(!cards.length) return;

    let workObserver = new IntersectionObserver(function(entries, observer) {
        entries.forEach(function(entry) {
            if(entry.intersectionRatio) {
                action(entry.target);
                observer.unobserve(entry.target)
            }
        })
    });

    cards.forEach(function(card) {
        workObserver.observe(card)
    })
}

export function workResizeHandler() {
    if(isDesktop()) {
        let cards = getCards('video[data-src]', '.card');

        if(!cards.length) return;

        cards.forEach(function(card) {
            videoAction(card)
        })
    }
}
