import cookie from 'cookie_js';

function titleCase(str) {
    let splitStr = str.toLowerCase().split(' ');

    for(let i = 0; i < splitStr.length; i++) {
        splitStr[i] = splitStr[i].charAt(0).toUpperCase() + splitStr[i].substring(1);
    }

    return splitStr.join(' ');
}

let sendMailForm = {
    data: {},

    saveData: function() {
        try {
            localStorage.sendMailData = JSON.stringify(sendMailForm.data)
        } catch(e) {
        }

        let sf = document.querySelector('#contact .submit_form');

        if(sf !== null) {
            if(this.isValidProperties('firstName', 'lastName', 'email')) {
                sf.removeAttribute('disabled');
            } else {
                sf.setAttribute('disabled', 'true')
            }
        }
    },

    validator: {
        get firstName() {
            return !!('' + (sendMailForm.data.firstName || '')).length;
        },
        get lastName() {
            return !!('' + (sendMailForm.data.lastName || '')).length;
        },
        get email() {
            return /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(sendMailForm.data.email);
        }
    },

    isValidProperties: function() {
        return !Array.prototype.slice.call(arguments)
            .filter(function(property) {
                return sendMailForm.validator.hasOwnProperty(property) &&
                    !sendMailForm.validator[property]
            }).length;
    }
};

function processForm(formData) {
    formData.append('form-name', 'contact-form');

    return fetch('/', {
        method: 'POST',
        headers: {'Content-Type': 'application/x-www-form-urlencoded'},
        body: new URLSearchParams(formData).toString()
    })
        .catch(err => {
            console.error(err)
            return null
        });
}

function contactPageHandler() {
    let view = document.getElementById('contact');
    let contactForm = document.querySelector('form');

    if(view === null || contactForm == null) {
        return
    }
    view.classList.remove('is-sending');
    view.classList.remove('is-sended');
    view.classList.remove('is-completed');
    view.classList.remove('is-failed');

    // inputs
    const fn = view.querySelector('#form_first-name');
    const ln = view.querySelector('#form_last-name');
    const em = view.querySelector('#form_email');
    const hfu = view.querySelector('#form_how-find-us');
    const ta = view.querySelector('textarea');

    try {
        sendMailForm.data = Object.assign(sendMailForm.data, JSON.parse(localStorage.sendMailData))
    } catch(e) {
    }

    function inputTrigger(input, dataName) {
        if(input && sendMailForm.data[dataName]) {
            input.value = sendMailForm.data[dataName];
        }

        function payload() {
            sendMailForm.data[dataName] = titleCase(this.value);
            sendMailForm.saveData()
        }

        ['input', 'keydown', 'keypress', 'keyup', 'change'].forEach(function(event) {
            input.addEventListener(event, payload)
        });
    }

    inputTrigger(fn, 'firstName');
    inputTrigger(ln, 'lastName');
    inputTrigger(em, 'email');
    inputTrigger(hfu, 'howFindUs');

    if(ta) ta.removeAttribute('style');

    sendMailForm.saveData();

    function submitEvent(e) {
        e.preventDefault();
        let node = view;
        node.classList.add('is-sending');
        node.classList.remove('is-failed');

        const inputs = Array.prototype.slice.call(
            node.querySelectorAll('textarea, input, button')
        );

        inputs.forEach(n => {
            n.setAttribute('disabled', 'true');
        });

        if(!sendMailForm.isValidProperties('firstname', 'lastname', 'email')) {
            inputs.forEach(n => {
                n.removeAttribute('disabled');
            });
            node.classList.remove('is-sending');
            return
        }

        let data = new FormData;

        Object.keys(sendMailForm.data).forEach(function(key) {
            data.append(key, sendMailForm.data[key])
        });

        ['fuid', 'source', 'campaign', 'conversion_page', 'internal_source'].forEach(i => {
            const el = view.querySelector(`input[name=${i}]`);

            if(el !== null) {
                data.append(i, el.value)
            }
        });

        function createError(nodeItem) {
            nodeItem.classList.remove('is-sending');
            nodeItem.classList.add('is-failed-state');
            nodeItem.classList.add('is-failed')
        }

        processForm(data)
            .then(function(res) {
                if(!res || res.status !== 200) {
                    throw res
                }

                /**
                 * @name dataLayer
                 */
                if(dataLayer) {
                    dataLayer.push({event: 'form_submission'})
                }

                node.classList.remove('is-sending');
                node.classList.remove('is-sended');

                const tFirstName = node.querySelector('.tFirstName');

                if(tFirstName !== null) {
                    const n = sendMailForm.data.firstName;
                    tFirstName.innerText = n ? `, ${n}` : '';
                }

                window.scrollTo({
                    top: 0,
                    left: 0,
                    behavior: 'smooth'
                });

                document.querySelector('header').removeAttribute('style');

                node.classList.remove('is-sended');
                node.classList.add('is-complete');

                setTimeout(function() {
                    [...node.querySelectorAll('textarea, input:not([type=checkbox]):not([type=radio])')]
                        .forEach(function(input) {
                            input.value = '';
                            input.removeAttribute('disabled');
                        });

                    [...node.querySelectorAll('input[type=checkbox], input[type=radio]')]
                        .forEach(function(input) {
                            this.checked = false;
                            this.removeAttribute('disabled')
                        });

                    sendMailForm.data = {};
                    sendMailForm.saveData()
                }, 1000)
            })
            .catch(function(error) {
                console.error(error);
                createError(node)
            });
    }

    contactForm.addEventListener('submit', e => {
        e.preventDefault();
    });

    const sb = [...document.querySelectorAll('.submit_form')];

    if(sb.length) {
        sb.forEach(s => {
            s.addEventListener('click', submitEvent)
        })
    }
}

export async function inputsTransliterateHandler(path) {
    if(path.indexOf('/contact') === 0) {
        let inputFuid = document.querySelector('input[name=fuid]');
        let inputSource = document.querySelector('input[name=source]');
        let inputCampaign = document.querySelector('input[name=campaign]');
        let inputConversion_page = document.querySelector('input[name=conversion_page]');
        let inputInternalSource = document.querySelector('input[name=internal_source]');

        const {default: transliterate} = await import('./_transliterate_m.js');

        [inputFuid, inputSource, inputCampaign, inputConversion_page, inputInternalSource]
            .filter(Boolean)
            .forEach(function(input) {
                if(input !== null) {
                    if(input.name === 'source') {
                        input.value = `${transliterate(cookie.get('source') || 'direct')
                        }/${transliterate(cookie.get('medium') || 'none')}`
                    } else if(input.name === 'conversion_page') {
                        if(document.referrer) {
                            input.value = transliterate(new URL(document.referrer).pathname || 'not set')
                        }
                    } else {
                        input.value = transliterate(cookie.get(input.name) || '')
                    }
                }
            })
    }
}

export default contactPageHandler;
