export default function() {
    function animate(list, onEnd) {
        let item;
        let duration;
        let end = 0;

        let step = function() {
            let current = +new Date();
            let remaining = end - current;

            if(remaining < 60) {
                if(item) item.run(1); // 1 = progress is at 100%

                item = list.shift(); // get the next item

                if(item) {
                    duration = item.time * 1000;
                    end = current + duration;
                    item.run(0); // 0 = progress is at 0%
                } else {
                    if(typeof onEnd === 'function') onEnd();
                    return;
                }
            } else {
                let rate = remaining/duration;
                rate = 1 - Math.pow(rate, 3); // easing formula
                item.run(rate);
            }

            requestAnimationFrame(step);
        };

        step();
    }

    function getChildrensHeight(wrap) {
        return [...wrap.children].reduce((acc, cur) => {
            acc += cur.offsetHeight;
            return acc
        }, 0);
    }

    class CollapseSection {
        constructor(node) {
            this.wrapper = node;
            this.li = [];
            this.activeLi = 0;
        }

        expand(item) {
            if(item !== null) {
                let end = getChildrensHeight(item.lastElementChild);
                item.classList.add('opened');

                animate([
                    {
                        time: .3,
                        node: item.lastElementChild,
                        start: 0,
                        end: end,
                        run: function(rate) {
                            this.node.style
                                .height = (rate * (this.end - this.start) + this.start) + 'px';
                        }
                    },

                ], () => {
                    let headerHeight = 93; // desktop header.offsetHeight
                    let itemTop = item.getBoundingClientRect().top;

                    if(itemTop < 0 || window.innerWidth < 768) {
                        window.scrollBy({
                            top: Math.ceil(itemTop - headerHeight),
                            left: 0,
                            behavior: 'smooth'
                        })
                    }
                });
            }
        }

        collapse(item) {
            if(item !== null && item.lastElementChild.offsetHeight !== 0) {
                let h = getChildrensHeight(item.lastElementChild);
                item.classList.remove('opened');

                animate([{
                    time: .3,
                    node: item.lastElementChild,
                    start: h,
                    end: 0,
                    run: function(rate) {
                        this.node.style
                            .height = (rate * (this.end - this.start) + this.start) + 'px';
                    }
                }]);

            }
        }

        updateItems(activeIndex) {
            let prevActive = this.activeLi;
            this.li.forEach((item, index) => {
                if(index !== activeIndex) {
                    this.collapse(item);
                } else if(item.classList.contains('opened')) {
                    item.classList.remove('opened');
                    this.collapse(item);
                } else {
                    this.expand(item, index > prevActive)
                    this.activeLi = index;
                }
            })
        }

        clickLiEvent(e) {
            e.preventDefault();

            const target = e.target;

            if(target) {
                const curLi = target.closest('li');
                if(!curLi) return;
                const curIndex = curLi.getAttribute('data-index');
                this.updateItems(Number(curIndex));
            }
        }

        init() {
            this.li = [...this.wrapper.children].filter(i => i.tagName === 'LI') || [];

            this.li.forEach((lItem, index) => {
                lItem.setAttribute('data-index', index)
                if(index !== 0) this.collapse(lItem);
                const button = lItem.querySelector('.collapse-control');
                if(button) button.addEventListener('click', this.clickLiEvent.bind(this), false);
            });

            window.addEventListener('resize', function() {
                const active = this.li[this.activeLi];
                if(active && active.classList.contains('opened')) {
                    active.lastElementChild.removeAttribute('style');
                }
            }.bind(this))
        }
    }

    const accordions = document.querySelectorAll('.section-collapse');

    if(accordions.length) {
        [...accordions].forEach(accordion => {
            new CollapseSection(accordion).init();
        })
    }
}
