import {isFunction, isPlainObject, TRANSITION_END_EVENT_TYPE} from "./_helpers";

function TransitionChain(name, finalHandler) {
    let that = this;

    that.name = name;
    that.finalHandler = finalHandler;
    that.chain = [];

    that.stopped = that.paused = true;

    that.__step = null;

    that.__nextStepIndex = 0;
    that.__transitionEnd = function (event) {
        if (
            !that.__step ||
            that.__step.node !== event.target ||
            isFunction(that.__step.handler) &&
            !that.__step.handler.call(that.__step.node, event)
        ) {
            return;
        }
        // event.stopPropagation();

        that.__step.node.classList.remove('transition-chain-state');
        that.__step.node.removeAttribute('data-transition-chain');
        that.next();
    };
}

Object.assign(TransitionChain.prototype, {
    next: function () {
        if (this.stopped || this.paused) {
            return;
        }

        if (this.__nextStepIndex === this.chain.length) {
            if (this.__step && isFunction(this.finalHandler)) {
                this.finalHandler.call(this.__step.node);
            }

            this.stop();
            return;
        }

        let step = this.chain[this.__nextStepIndex++];

        if (!isPlainObject(step)) {
            step = {
                node: step
            };
        }

        step = Object.assign({}, step);

        if (isFunction(step.node)) {
            step.node = step.node();
        }

        if (step.node) {
            step.node.setAttribute('data-transition-chain', this.name);
            step.node.classList.add('transition-chain-state');
            this.__step = step;
        } else {
            this.next();
        }
    },

    run: function () {
        return this.stop().play();
    },

    play: function () {
        if (this.stopped) {
            document.addEventListener(TRANSITION_END_EVENT_TYPE, this.__transitionEnd)
        }

        this.stopped = this.paused = false;
        this.next();

        return this;
    },

    stop: function () {
        if (!this.stopped) {
            document.removeEventListener(TRANSITION_END_EVENT_TYPE, this.__transitionEnd);

            this.stopped = true;
            this.__step = null;
            this.__nextStepIndex = 0;
        }

        return this;
    },

    pause: function () {
        this.paused = true;

        return this;
    }
});

export default TransitionChain;
